import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import vuetify from "./plugins/vuetify";
import scrollSpy, { Easing } from "vue2-scrollspy";

Vue.use(scrollSpy, {
  easing: Easing.Cubic.In,
});

Vue.config.productionTip = false;

new Vue({
  router,
  vuetify,
  render: (h) => h(App),
}).$mount("#app");
